.app-bar {
  width: 120px;
  background-color: #4b4f54;
  color: white;
  display: flex;
  flex-direction: column;
  /*justify-content: center;*/
  align-items: center;
  padding: 20px 0;
  position: relative;
}

@media screen and (max-width: 1024px) {
    .app-bar {
        width: 90px;
    }
}

.menu-icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: auto;
}

.menu-icon span {
	margin-top: 10px;
	font-size: 24px;
}

.logo {
  position: absolute;
  left: -90px;
  top: 50%;
  margin-top: -102px;
}

.cta-icons {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: auto;
}

.icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px 0;
}

.icon span {
  font-size: 16px;
  color: #ffbd59;
}

.icon .fa-icon {
  margin-top: 10px;
  color: white;
}

.icon:hover{
	cursor: pointer;
}